@import "./fonts.scss";

$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: bold 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;
}

// first section
.welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    color: $grey1;
    margin: 0;
  }

  p {
    font: 420 20px Brandon Grotesque;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 64px auto 0;
    min-height: 595px;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 595px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        .block-about_body {
          margin: 0 32px;

          .schema_inner {
            padding-top: 105px;

            .schema_screens {
              justify-content: space-evenly;
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 325px;
                top: -105px;
                background-color: $grey1;
              }

              .schema_body {
                .schema_title {
                  font: 900 16px "Brandon Grotesque";
                  line-height: 24px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  margin-top: 24px;
                }
              }

              .arrow {
                padding-top: 6px;
                z-index: 2;
              }
            }

            .schema_cards {
              position: relative;
              z-index: 2;
              margin-top: 48px;
              .schema_card {
                position: relative;
                width: 144px;
                height: 176px;
                background: $yellow;
                border-radius: 10px;

                &:nth-child(2),
                &:nth-child(3) {
                  &:before {
                    content: "";
                    position: absolute;
                    width: 50px;
                    height: 4px;
                    left: -48px;
                    background-color: #fcbd3b;
                    z-index: -2;
                  }
                }

                &:after {
                  content: " ";
                  position: absolute;
                  width: 144px;
                  height: 176px;
                  left: 8px;
                  top: 9px;
                  z-index: -1;
                  background: $grey1;
                  border-radius: 10px;
                }

                .schema_title {
                  font: 900 13px Brandon Grotesque;
                  line-height: 20px;
                  margin-top: 23px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;

                  color: $grey1;
                }
              }
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            h3 {
              padding-top: 63px;
              font: 900 34px Brandon Grotesque;
              line-height: 48px;
              color: $grey1;
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              .list_item {
                list-style: none;
                margin-bottom: 32px;
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: 420 24px Brandon Grotesque;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;
                }
              }
            }
            .list_line {
              position: absolute;
              top: 172px;
              bottom: 12%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 594px;
          background: $grey1;
          border-radius: 20px;
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px Brandon Grotesque;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

// second section
.youNeedIt {
  background-color: $blue;
  padding-bottom: 59px;
  position: relative;

  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }

  h3 {
    font: 900 32px Brandon Grotesque;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 99px;
  }

  .cards {
    margin-top: 40px;

    .card_item {
      width: 445px;
      height: 128px;
      background: $bloomarin;
      border-radius: 10px;
      margin-bottom: 33px;

      h4 {
        font: 900 20px Brandon Grotesque;
        line-height: 28px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 0 0 0 24px;
      }
    }
  }
  .image {
    position: absolute;
    right: 2.5%;
    bottom: -23px;
    width: 237px;
    height: 222px;
    background: #fcbd3b;
    border-radius: 10px;

    img {
      position: absolute;
      right: 14px;
      top: 12px;
    }
  }
}

// third section
.migrations {
  padding-bottom: 130px;
  .poster {
    margin-top: 186px;
    width: 100%;
    height: 815px;
    background: $pink;
    border: 6px solid #000000;
    box-sizing: border-box;
    border-radius: 20px;

    h3 {
      margin-top: 130px;
    }

    .poster_body {
      padding: 64px 116px 0;
      .list {
        padding-left: 25px;
        .list_item {
          list-style: none;
          margin-bottom: 32px;
          position: relative;
          &:nth-child(2),
          &:nth-child(3) {
            &:after {
              content: "";
              position: absolute;
              top: 50%;
              left: -25px;
              transform: translateY(-50%);
              width: 20px;
              height: 3px;
              background-color: #000000;
            }
          }
          .list_img {
            margin-right: 13px;
          }
          .list_title {
            font: 420 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            color: $grey1;
          }
        }
      }
      .list_line {
        position: absolute;
        top: 30px;
        bottom: 59.5%;
        width: 20px;
        border-radius: 7px 0 0 7px;
        border-left: 3px solid #000;
        border-top: 3px solid #000;
        border-bottom: 3px solid #000;
      }

      .card {
        width: 489px;
        height: 717px;
        background: #fc6839;
        border-radius: 20px;
        position: relative;
        .card_body {
          padding: 50px 50px 0;

          h4 {
            font: 900 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            color: #ffffff;
          }
          p {
            font-family: 420 20px Brandon Grotesque;
            line-height: 30px;
            margin-top: 24px;
            letter-spacing: 0.3px;
            color: #ffffff;
          }

          a {
            position: relative;
            font: bold 24px Brandon Grotesque;
            line-height: 32px;
            letter-spacing: 0.3px;
            text-decoration-line: underline;
            color: #0a0a0a;
            margin-top: 16px;
            cursor: pointer;
            z-index: 2;
          }

          .image {
            position: absolute;
            right: -1px;
            bottom: -2px;
            left: -1px;
            border-radius: 20px;
            overflow: hidden;
            z-index: 1;
            img {
              width: 100%;
            }
          }
        }
      }
    }
  }
}

.application {
  background: $bloomarin;
  padding-bottom: 145px;
  h3 {
    padding-top: 170px;
    margin-top: 0;
  }
  p {
    font: 420 20px Brandon Grotesque;
    line-height: 28px;
    text-align: center;
    letter-spacing: 0.3px;
    color: $grey1;
    margin-top: 40px;
  }
  .cadrs_body {
    width: 100%;
    margin-top: 46px;

    .card {
      position: relative;
      width: 1107px;
      height: 280px;
      background: $pink;
      border-radius: 20px;
      margin-bottom: 41px;

      &::after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translate(-50%, 100%);
        border: 20px solid transparent;
        border-top: 20px solid $pink;
      }

      .card_image {
        margin: 32px 0 38px;
      }

      .card_title {
        font: 900 20px Brandon Grotesque;
        line-height: 28px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin: 0 0 32px;
      }
      p {
        font: 420 20px Brandon Grotesque;
        line-height: 30px;
        text-align: center;
        letter-spacing: 0.3px;
        color: $grey1;
        mix-blend-mode: normal;
        margin: 0;
      }
    }
  }
}

.transition {
  position: relative;
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $bloomarin;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  .transition_body {
    padding-top: 162px;

    h3 {
      font: normal 58px Playfair Display;
      color: $grey1;
      margin-bottom: 32px;
    }
    p {
      font: normal 20px Brandon Grotesque;
      line-height: 28px;
      letter-spacing: 0.3px;
      color: $grey1;
      margin-bottom: 79px;
    }

    .note {
      position: absolute;
      bottom: -25px;
      left: 34px;
      width: 257.09px;
      height: 212.59px;

      img {
        position: absolute;
      }
      h5 {
        position: absolute;
        top: 25%;
        left: 27px;
        font: 900 24px Brandon Grotesque;
        line-height: 24px;
        align-items: center;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
      h6 {
        position: absolute;
        bottom: -9%;
        left: 36px;
        font: 420 14px Brandon Grotesque;
        line-height: 20px;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
    }
  }
  .principles_body {
    position: relative;
    width: 576px;
    height: 466px;
    background: $pink;
    border-radius: 20px;
    margin: 37px 0 130px;

    padding-bottom: 130px;
    .block_technical {
      position: absolute;
      top: -22px;
      left: 30px;

      img {
        &:first-child {
          margin-right: 58.21px;
        }
        &:last-child {
          margin-left: 49.22px;
        }
      }
    }

    h4 {
      padding: 157px 0 0 32px;
      font: 900 20px Brandon Grotesque;
      line-height: 28px;
      align-items: center;
      letter-spacing: 0.3px;
      text-transform: uppercase;
      color: $grey1;
    }

    ul {
      margin-top: 40px;
      padding-left: 55px;
      li {
        list-style: $grey1;
        font: 420 20px Brandon Grotesque;
        line-height: 30px;
        letter-spacing: 0.3px;
        color: $grey1;
        margin-bottom: 15px;
      }
    }
  }
}

